import React, { Component } from 'react';

import Navbar from '../components/Navbar';

export default class Entreprise extends Component {
    render() {
        return(
            <div>
                <Navbar active="entreprise"/>
                <div className="container">
                    <h2>MATRASUR COMPOSITES</h2>
                    <p>Matrasur Composites fournit le march des composites depuis 1972 et fait partie des premires entreprises franaises  stre spcialise dans les quipements pour la mise en oeuvre des matriaux composites. Aujourdhui, elle se distingue comme un leader dans le domaine.</p>
                </div>
            </div>
        );
    }
}


